.member_tit {
	border-bottom: 1px solid #ccc;
	padding-bottom: 50px;
	width: 968px;
	margin: 0 auto;
}

.member_tit dl {
	display: table;
	table-layout: fixed;
	width: 100%;
	padding-left: 80px;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
			box-sizing: border-box;
}

.member_tit dl > dt,
.member_tit dl > dd {
	display: table-cell;
	vertical-align: middle;
}

.member_tit dl > dt {
	width: 160px;
}

.member_tit dl > dd h5 {
	font-size: 2.133rem;
	word-break: keep-all;
	line-height: 1.4;
}

.member_tit dl > dd h5 span {
	color: #6569bc;
}

.member_tit dl > dd p {
	color: #666;
	font-size: 1.2rem;
	margin-top: 5px;
}

.member_tit dl > dd p.bottom_txt {
	position: relative;
	top: 30px;
}

.member_tit.finished {
	border: none;
}

.member_tit.finished p {
	margin-bottom: 10px;
}

.login_form {
	padding: 50px 0;
}

.login_form .login_box {
	width: 550px;
	display: table;
	margin: 0 auto;
}

.login_form .login_box > ul,
.login_form .login_box > span {
	display: table-cell;
	vertical-align: top;
}

.login_form .login_box > span {
	width: 140px;
}

.login_form .login_box button {
	background: #45495b;
	color: #fff;
	font-size: 1.133rem;
	width: 100%;
	height: 82px;
}

.login_form .login_box li {
	overflow: hidden;
}

.login_form .login_box li:first-child {
	margin-bottom: 5px;
}

.login_form .login_box label {
	width: 20%;
	display: inline-block;
	float: left;
	font-weight: bold;
	font-size: 1.067rem;
	line-height: 38px;
}

.login_form .login_box input[type="password"],
.login_form .login_box input[type="text"],
.login_form .login_box input[type="tel"] {
	float: left;
	width: 78%;
	height: 36px;
	border: 1px solid #9d9d9d;
	padding: 0 5px;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
			box-sizing: border-box;
}

.login_form .login_link {
	width: 550px;
	overflow: hidden;
	margin: 15px auto 0;
	padding-left: 140px;
}

.login_form .login_link a {
	float: left;
	background-image: url("../images/member/member_bul.png");
	-webkit-background-size: auto auto;
			background-size: auto;
	background-repeat: no-repeat;
	background-position: left 0.4rem;
	padding-left: 12px;
	margin-right: 25px;
	font-size: 1.067rem;
}

.join_link {
	padding-top: 40px;
	border-top: 1px dashed #c1c1c1;
	text-align: center;
}

.join_link .join_link_box {
	display: inline-block;
	overflow: hidden;
}

.join_link p {
	color: #666;
	font-size: 1.067rem;
	margin-right: 10px;
	float: left;
	line-height: 35px;
}

.join_link a {
	display: inline-block;
	width: 125px;
	height: 35px;
	line-height: 35px;
	text-align: center;
	border: 1px solid #ccc;
	font-size: 1.133rem;
	float: left;
}

.certifi_wrap {
	text-align: center;
	padding-top: 50px;
}

.certifi_wrap .certifi_box {
	overflow: hidden;
	display: inline-block;
}

.certifi_wrap .certifi_box dl {
	border: 1px solid #ccc;
	width: 408px;
	float: left;
}

.certifi_wrap .certifi_box dl dt {
	padding: 50px 0 30px 0;
	text-align: center;
}

.certifi_wrap .certifi_box dl dd {
	background: #46495c;
	padding: 40px 60px 30px 60px;
	height: 136px;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
			box-sizing: border-box;
	word-break: keep-all;
}

.certifi_wrap .certifi_box dl dd h5 {
	color: #fff;
	font-size: 1.333rem;
	margin-bottom: 8px;
}

.certifi_wrap .certifi_box dl dd p {
	color: #fff;
	font-size: 1.067rem;
	line-height: 1.4;
}

.certifi_wrap .certifi_box a:first-child dl {
	margin-right: 10px;
}

.certifi_wrap .certifi_box a:first-child dl dd {
	background: #6469bb;
}

.result_mb {
	border-bottom: none;
}

.result_mb dl h5 {
	font-size: 2.133rem;
	letter-spacing: -1px;
	line-height: 1.3;
	margin-bottom: 10px;
}

.mb_btn {
	min-width: 140px;
	height: 34px;
	line-height: 34px;
	font-size: 1.133rem;
	text-align: center;
	padding: 0 15px;
	display: inline-block;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
			box-sizing: border-box;
}

.mb_btn.gray_btn {
	background: #45495b;
	border: 1px solid #45495b;
	color: #fff;
}

.mb_btn.ccc_btn {
	background: #fff;
	border: 1px solid #ccc;
	color: #333;
}

.mb_step_wrap {
	border-bottom: 1px solid #ccc;
	text-align: center;
	margin-bottom: 30px;
}

.mb_step_wrap .mb_step_box {
	display: inline-block;
}

.mb_step_wrap .mb_step_box ul {
	overflow: hidden;
	padding: 9px 0 45px;
}

.mb_step_wrap .mb_step_box ul > li {
	float: left;
	padding-left: 70px;
	position: relative;
}

.mb_step_wrap .mb_step_box ul > li:after {
	content: '';
	width: 50px;
	height: 1px;
	background: #ccc;
	position: absolute;
	left: 7%;
	top: 35%;
}

.mb_step_wrap .mb_step_box ul > li:first-child {
	padding-left: 0;
}

.mb_step_wrap .mb_step_box ul > li:first-child:after {
	display: none;
}

.mb_step_wrap .mb_step_box ul > li .mb_step_ic {
	width: 70px;
	height: 70px;
	border-radius: 70px;
	background: #ccc;
	display: table;
	margin: 0 auto;
}

.mb_step_wrap .mb_step_box ul > li .mb_step_ic span {
	display: table-cell;
	vertical-align: middle;
}

.mb_step_wrap .mb_step_box ul > li p {
	font-size: 1.067rem;
	position: relative;
	margin-top: 10px;
	color: #333;
}

.mb_step_wrap .mb_step_box ul > li.mb_step_on .mb_step_ic {
	background: #6569bc;
}

.mb_step_wrap .mb_step_box ul > li.mb_step_on p {
	color: #6569bc;
}

.mb_step_wrap .mb_step_box ul > li.mb_step_on p:after {
	content: '';
	position: absolute;
	top: 30px;
	left: 0;
	width: 0px;
	height: 0px;
	border-bottom: 10px solid #6569bc;
	border-top: none;
	border-right: 10px solid transparent;
	border-left: 10px solid transparent;
	right: 0;
	margin: 0 auto;
}

.agr_link {
	text-align: center;
	margin-top: 50px;
}

.agr_link > a {
	display: inline-block;
